/* eslint-disable no-use-before-define */
import * as React from "react";
import styled from "styled-components";
import { MdRadioButtonUnchecked } from "react-icons/md";
import { Box } from "./Box";
import { Flex } from "./Flex";

const afterStyle = ({ hasChildren, theme }) => {
  if (hasChildren) {
    return null;
  }

  return `
      &:after {
        content: "";
        position: absolute;
        width: 1px;
        background: ${theme.colors.secondary};
        bottom: 0;
        height: 12px;
        left: 6px;
      }
    `;
};

const adjacentSiblingWithChildren = ({ hasChildren }) => {
  if (!hasChildren) {
    return null;
  }

  return `
    & + li:before {
     content: unset!important;
    }
  `;
};

const TimelineWrapper = styled.ul``;

const TimelineItemWrapper = styled.li`
  position: relative;
  padding: ${(p) => p.theme.spaces[2]} 0;
  line-height: ${(p) => p.theme.lineHeights.text};

  &:not(&:first-of-type):before {
    content: "";
    position: absolute;
    width: 1px;
    background: ${(p) => p.theme.colors.secondary};
    top: 0;
    height: 12px;
    left: 6px;
  }

  &:not(&:last-of-type) {
    ${afterStyle};
  }

  ${adjacentSiblingWithChildren};

  svg {
    font-size: 13px;
    position: relative;
    margin-top: 3px;
  }

  ${TimelineWrapper} > li:first-of-type {
    margin-top: ${(p) => p.theme.spaces[2]};
    &:before {
      content: "";
      position: absolute;
      width: 1px;
      background: ${(p) => p.theme.colors.secondary};
      height: 34px;
      transform: rotateZ(-32deg) translate3d(8px, -29px, 0);
    }
  }

  ${TimelineWrapper} > li:last-of-type {
    margin-bottom: -${(p) => p.theme.spaces[2]};
    &:after {
      content: "";
      position: absolute;
      width: 1px;
      background: ${(p) => p.theme.colors.secondary};
      height: 34px;
      transform: rotateZ(32deg) translate3d(-10px, 0px, 0);
    }
  }
`;

export const TimelineItem = ({ children, ...props }) => (
  <TimelineItemWrapper {...props}>
    <Flex>
      <Box marginRight={3}>
        <MdRadioButtonUnchecked />
      </Box>

      {children}
    </Flex>
  </TimelineItemWrapper>
);

export const Timeline = ({ children }) => {
  return <TimelineWrapper>{children}</TimelineWrapper>;
};
