import { useRef, useContext, useEffect } from "react";
import { KeyboardKey } from "../../modules/a11y/keyboard";
import { MenuButtonContext } from "./context";
import { focusNextItem } from "../../modules/a11y/helpers/focusNextItem";
import { focusPreviousItem } from "../../modules/a11y/helpers/focusPreviousItem";
import { focusFirstItem } from "../../modules/a11y/helpers/focusFirstItem";
import { focusLastItem } from "../../modules/a11y/helpers/focusLastItem";
import { focusStartingWith, MENU_ITEMS_SELECTOR } from "./helpers";

export const useMenuListFocus = () => {
  const menuListRef = useRef(null);
  const { expandedState, toggle } = useContext(MenuButtonContext);

  // Focus the next / previous menu item when pressing arrow up / down
  const onKeyDown = (e) => {
    switch (e.key) {
      case KeyboardKey.DOWN:
        if (!menuListRef.current) return;
        e.preventDefault();
        focusNextItem(menuListRef.current, MENU_ITEMS_SELECTOR);
        break;

      case KeyboardKey.UP:
        if (!menuListRef.current) return;
        e.preventDefault();
        focusPreviousItem(menuListRef.current, MENU_ITEMS_SELECTOR);
        break;

      case KeyboardKey.ESCAPE:
        toggle("CLOSED");
        break;

      default:
        if (e.key.length > 1) return;
        if (!menuListRef.current) return;

        e.stopPropagation();
        focusStartingWith(menuListRef.current, e.key);
        break;
    }
  };

  // Focus the first or last element when pressing expanded state changes (when the dropdown appears)
  useEffect(() => {
    if (!menuListRef.current) return;

    if (expandedState === "CLOSED") {
      menuListRef.current?.previousSibling.focus();
    } else if (expandedState === "DOWN") {
      focusFirstItem(menuListRef.current, MENU_ITEMS_SELECTOR);
    } else {
      focusLastItem(menuListRef.current, MENU_ITEMS_SELECTOR);
    }
  }, [expandedState]);

  // Handle leaving the focus of the menu list
  useEffect(() => {
    const actualRef = menuListRef.current;
    if (!actualRef) return;

    const handleOut = (e) => {
      const focusTarget = e.relatedTarget;

      if (
        !focusTarget ||
        focusTarget.getAttribute("role") !== "menuitemradio"
      ) {
        toggle("CLOSED");
      }
    };

    actualRef.addEventListener("focusout", handleOut);

    return () => {
      actualRef.removeEventListener("focusout", handleOut);
    };
  }, [toggle]);

  return { onKeyDown, menuListRef, expandedState };
};
