export const MENU_ITEMS_SELECTOR = '[role="menuitemradio"]:not([disabled])';

const startWith = (element, char) => {
  const lowerKey = char.toLowerCase();
  const lowerContent = element.textContent?.toLowerCase();

  return lowerContent?.startsWith(lowerKey);
};

export const focusStartingWith = (menuNode, firstLetter) => {
  const menuItems = Array.from(menuNode.querySelectorAll(MENU_ITEMS_SELECTOR));
  const matchingIndex = menuItems.findIndex((menuItem) =>
    startWith(menuItem, firstLetter)
  );

  const matching = menuItems[matchingIndex];

  if (!matching) return;

  // If it's already focused, try to focus the next one with the same letter
  if (matching === document.activeElement) {
    const nextSibling = matching.nextSibling;

    if (startWith(nextSibling, firstLetter)) {
      nextSibling.focus();
    }
  } else {
    matching.focus();
  }
};
