import * as React from "react";
import { Timeline, TimelineItem } from "../../components/Timeline";
import { Link } from "../../components/Link";
import { Text } from "../../components/Text";

export const ModuleTimeline = ({ links, LinkAs = Link }) => {
  return (
    <Timeline>
      {links.map((course) => {
        const hasChildren = course?.children?.length > 0;

        return (
          <TimelineItem
            key={course.link || course.title}
            hasChildren={hasChildren}
          >
            <div>
              {course.link ? (
                <LinkAs to={course.link}>{course.title}</LinkAs>
              ) : (
                <Text as="span">{course.title}</Text>
              )}

              {hasChildren && (
                <ModuleTimeline links={course.children} LinkAs={LinkAs} />
              )}
            </div>
          </TimelineItem>
        );
      })}
    </Timeline>
  );
};
