import React, { useContext, useState } from "react";
import styled from "styled-components";
import { KeyboardKey } from "../../modules/a11y/keyboard";
import { MenuButtonContext } from "./context";
import { useMenuListFocus } from "./useMenuListFocus";
import { MdKeyboardArrowDown, MdCheck } from "react-icons/md";

const MenuWrapper = styled.div`
  position: relative;
  display: inline-block;
`;

export const Menu = ({ children, ...props }) => {
  const [expandedState, setExpandedState] = useState(undefined);

  const toggle = (value) => {
    setExpandedState(value);
  };

  return (
    <MenuWrapper {...props}>
      <MenuButtonContext.Provider value={{ expandedState, toggle }}>
        {children}
      </MenuButtonContext.Provider>
    </MenuWrapper>
  );
};

const MenuListWrapper = styled.div`
  position: absolute;
  right: 0;
  box-shadow: ${(p) => p.theme.shadows.simple};
  border-radius: ${(p) => p.theme.radius[1]};
  background: ${(p) => p.theme.colors.textInverse};
  width: max-content;
`;

export const MenuList = ({ position = "left", ...props }) => {
  const { menuListRef, onKeyDown, expandedState } = useMenuListFocus();

  const hidden = !expandedState || expandedState === "CLOSED";

  return (
    <MenuListWrapper
      {...props}
      role="menu"
      ref={menuListRef}
      onKeyDown={onKeyDown}
      hidden={hidden}
    />
  );
};

const MenuButtonWrapper = styled.button`
  background: transparent;
  border: none;
  padding: ${(p) => p.theme.spaces[2]};
  display: flex;
  align-items: center;
  color: inherit;
  font-size: ${(p) => p.theme.fontSizes[1]};

  svg {
    margin-left: ${(p) => p.theme.spaces[0]};
  }
`;

export const MenuButton = ({ children, ...props }) => {
  const { expandedState, toggle } = useContext(MenuButtonContext);

  const handleMouseDown = (e) => {
    // Check if the right click has been clicked
    // "which" check is for webkit
    if (e.nativeEvent.which === 3 || e.nativeEvent.button === 2) {
      return;
    }

    e.target.focus();

    if (!expandedState || expandedState === "CLOSED") {
      toggle("DOWN");
    } else {
      toggle("CLOSED");
    }
  };

  const handleKeyCode = (e) => {
    switch (e.key) {
      case KeyboardKey.DOWN:
        e.preventDefault();
        toggle("DOWN");
        break;

      case KeyboardKey.UP:
        e.preventDefault();
        toggle("UP");
        break;

      case KeyboardKey.ENTER:
      case KeyboardKey.SPACE:
        toggle("DOWN");
        break;

      default:
        break;
    }
  };

  const expanded = expandedState === "UP" || expandedState === "DOWN";

  return (
    <MenuButtonWrapper
      {...props}
      aria-haspopup={true}
      aria-expanded={Boolean(expanded)}
      onMouseDown={handleMouseDown}
      onKeyDown={handleKeyCode}
    >
      <span>{children}</span>
      <MdKeyboardArrowDown aria-hidden={true} />
    </MenuButtonWrapper>
  );
};

const MenuItemWrapper = styled.button`
  padding: ${(p) => p.theme.spaces[2]} ${(p) => p.theme.spaces[3]};
  background: transparent;
  border: none;
  display: flex;
  align-items: center;
  width: 100%;
  text-align: left;
  font-size: ${(p) => p.theme.fontSizes[1]};

  svg {
    margin-left: ${(p) => p.theme.spaces[2]};
  }
`;

export const MenuItem = ({ onSelect, children, selected, ...props }) => {
  const { toggle } = useContext(MenuButtonContext);

  const handleMouseDown = () => {
    if (onSelect) {
      onSelect();
    }

    toggle("CLOSED");
  };

  const handleKeyCode = (e) => {
    switch (e.key) {
      case KeyboardKey.ENTER:
      case KeyboardKey.SPACE:
        handleMouseDown();
        break;
      default:
        break;
    }
  };

  return (
    <MenuItemWrapper
      {...props}
      role="menuitemradio"
      tabIndex={-1}
      onMouseDown={handleMouseDown}
      onKeyDown={handleKeyCode}
      aria-checked={selected}
    >
      {children} {selected && <MdCheck aria-hidden={true} />}
    </MenuItemWrapper>
  );
};
