import React, { useContext } from "react";
import {
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
} from "../../components/MenuButton/MenuButton";
import { themeList } from "../../themes/themes";
import { SetThemeContext } from "./context";

export const ThemeMenuButton = () => {
  const { actualTheme, setActualTheme } = useContext(SetThemeContext);

  return (
    <Menu>
      <MenuButton>Change theme</MenuButton>
      <MenuList>
        {themeList.map((theme) => (
          <MenuItem
            key={theme.name}
            onSelect={() => setActualTheme(theme)}
            selected={theme.name === actualTheme.name}
          >
            {theme.name}
          </MenuItem>
        ))}
      </MenuList>
    </Menu>
  );
};
